// -------------------------------------------------HEader------------------------------------------------------------------------
header{
    display: flex !important;
    justify-content: start;
    max-width: 1280px;
    font-family: 'Roboto', sans-serif;
    margin: 0 auto;
    svg {
    margin-top: 5px;
    color: #f67e79;
    }
    .openMobilMenu{
        @media all and (max-width: 992px){
            display: block;
        }
        display: none;
        margin-left: auto;
        margin-top: 40px;
        margin-right: 20px;
    }
    .logo{
        @media all and (max-width:992px) {
            text-align: center;
            width: 50px;
            margin: 20px auto;
        }
        flex-grow: 0;
        text-align: center;
        width: 22%;
        img{
            width: 203px;
            max-width: inherit ;
            display: inline-block;
            margin-top: 20px;
            border: none;
            
        }
    }
    .city{
        @media all and (max-width:992px) {
            display: none !important;
            
        }
        font-size: 20px;
        padding: 25px 0 25px 30px;
        position: relative;
        display: flex;
        p{
            color: #f67e79;
        }
    }
    .mail{
        @media all and (max-width:992px) {
            display: none !important;
        }
        color: #a5a5a5;
        font-size: 20px;
        padding: 25px 0 25px 30px;
        position: relative;
        display: flex;
        p{
            margin-left: 5px;
        }
    }
    .tell{
        @media all and (max-width:992px) {
            display: none !important;
        }
        font-size: 20px;
        padding: 25px 0 25px 30px;
        position: relative;
        display: flex;
        a{
            color: #a5a5a5 ;
            &:hover{
                text-decoration: none;
            }
        }
        svg{
            color: #f67e79;
        }
    }
    .call_back{
        @media all and (max-width:992px) {
            display: none !important;
        }
        width: 200px;
        margin: 0 20px;
        text-align: center;
        display: inline-block;
        height: 80px;
        background: linear-gradient(90deg,#fd4304,#ed073c);
        position: relative;
        cursor: pointer;
        span{
            display: block;
            position: relative;
            padding: 26px 0;
            font-weight: 600;
            color: #FFF;
        }
    }
    .change_lang{
        @media all and (max-width:992px) {
            display: none !important;
        }
        font-size: 20px;
        padding: 25px 0 25px 30px;
        position: relative;
        display: flex;
        .active{
            color: #f67e79;
        }
        a{
            margin: 0 8px;
            color: #a5a5a5a5;
            font-weight: 600;
            &:hover{
                text-decoration: none;
            }
        }
    }
    
}
//-----------------------------------------------------Navbar-----------------------------------------------------------
.nav_header{
    @media all and (max-width:992px){
        display: none !important;
    }
    background-color: #F7F7F7;
    width: 100%;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    margin-top: -15px;
    nav{
        ul{
            display: flex !important;
            justify-content: center;
            .services{
                .servicesBtnn{
                    background-color: transparent !important;
                    border: none;
                    color: #000;
                    font-size: 22px;
                    font-weight: 400;
                    height: 30px;
                    transition: 0;
                    padding: 0;
                    &:focus{
                        outline: none !important;
                        border: none !important;
                        box-shadow: none;
                    }
                    &:hover{
                        color: #FC3D0B;
                    }
                }
                .dropdownmenu{
                    width: 250px;
                    border: none;
                    margin-top: 16px;
                    left: -32px !important;
                    &:hover{
                        box-shadow: 5px;
                        cursor: pointer;
                    }
                    .DropdownItem{
                        padding-left: 30px;
                        background-color: #FFF;
                        &:hover{
                            background-color: rgb(248, 246, 246);
                            color: #ff3300;
                        }
                    }
                    a{
                        font-size: 17px;
                        color: black;
                    }
                }
            }
            li{
                list-style-type: none;
                padding: 14px 34px !important;
                &:hover{
                    text-decoration: none;
                    background-color: #FFF;
                    cursor: pointer;
                    a{
                        text-decoration: none;
                        color: #FC3D0B;
                    }
                }
                a{
                    font-size: 23px;
                    color: #000;
                    font-weight: 400;
                }
            }
            }
         }
    }
.mobil_nav{
    display: none;
    width: 100%;
    padding-top: 20px;
    z-index: 9999;
    position: fixed;
    height: 600px;
    transition: 1s ease-in-out;
    background-color: #FFF;
    color: #000;
    .header{
        display: flex;
        .change_lang{
            .active{
                color: #FC3D0B;
            }
            a{
                
                color: #c8c7c7;
                padding: 20px;
            }
        }
        .location{
            display: flex;
            svg{
                color: #FC3D0B;
            }
        }
        .close{
            margin-left: auto;
            margin-right: 20px;
            svg{
                color: #808080;
                font-size: 28px;
            }
        }
    }
    .main{
        ul{
           
            li{
                list-style-type: none;
                padding: 15px 15px;
                border-bottom: 1px solid #c8c7c7;
                a{
                    color: #000;
                   
                }
            }
        }
    }
}
